
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import {mapState, mapActions} from 'vuex'
import {getAssets} from '@/modules/user/api/get_user'
import {showloadings, hideloadings} from '@/utils/load'

const imgSrc = {
FBC: {
src: require('@/assets/img/currency/btc.png'),
fullName: 'Fortune Coin'
},
BTC: {
src: require('@/assets/img/currency/btc.png'),
fullName: 'Bitcoin'
},
ETH: {
src: require('@/assets/img/currency/eth.png'),
fullName: 'Ethereum'
},
EOS: {
src: require('@/assets/img/currency/eos.png'),
fullName: 'Eos'
},
USDT: {
src: require('@/assets/img/currency/usdt.png'),
fullName: 'TetherUS'
},
ZL: {
src: require('@/assets/img/currency/usdt.png'),
fullName: 'ZL'
},
TEST: {
src: require('@/assets/img/currency/usdt.png'),
fullName: 'TEST'
}
}

export default {
name: 'User',
data() {
return {
imgList: [],
assetsList: []
}
},
created() {

},
beforeRouteEnter(from, to, next) {
next(vm => {
vm.getAssetsData()
})
},
methods: {
...mapActions([
'showWallet',
'setWallet'
]),
linkFinance() {
this.$router.push({path: '/finance'})
},
getDescribe(item) {
this.showWallet(item)
this.$router.push({
path: '/account/show',
query: {
item: item
}
})
},
getAssetsData() {
showloadings()
getAssets().then((res) => {
if (res.status === 200) {
const data = this.addText(res.data.data)
this.setWallet(data)
this.assetsList = data
hideloadings()
}
})
},
addText(data) {
const vdata = data
for (let i = 0; i < vdata.length; i++) {
const currency = vdata[i].currency.toUpperCase()
const link = 'src'
const fullName = 'fullName'
vdata[i][link] = imgSrc[currency].src
vdata[i][fullName] = imgSrc[currency].fullName
}
return vdata
}
},
computed: {
...mapState({

})
},
watch: {},
components: {
Group,
XHeader,
Cell
}
}



